import Vue from 'vue'
import VueRouter from 'vue-router'

import index from '../views/index.vue'
import index2 from '../views/index2.vue'
import index3 from '../views/index3.vue'
import index4 from '../views/index4.vue'
import index5 from '../views/index5.vue'
import index6 from '../views/index6.vue'
import teaxq from '../views/teaxq.vue'

Vue.use(VueRouter)

const routes = [
  {path:'/',name:'index',component:index},
  {path:'/index2',name:'index2',component:index2},
  {path:'/index3',name:'index2',component:index3},
  {path:'/index4',name:'index2',component:index4},
  {path:'/index5',name:'index2',component:index5},
  {path:'/index6',name:'index2',component:index6},
  {path:'/teaxq/:xq',name:'teaxq',component:teaxq},
]


const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes,
})
export default router
